html {
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navBar {
  width: 100%;
  height: 7rem;
  margin-top: -2.7rem;
  background-color: rgb(51, 51, 51);
}

.navBar h1 {
  color: white;
  line-height: 7rem;
  text-align: left;
  padding-left: 4rem;
  font-size: 4rem;
}

.main {
  display: flex;
  gap: 3rem;
  background-color: rgb(204, 204, 204);
  padding: 3rem 6rem;
  height: auto;
}

.leftDiv {
  background-color: rgb(238, 238, 238);
  padding: 1rem;
  border-radius: 10px;
  width: 50%;
  box-shadow: rgb(0 0 0 / 35%) 0px 5px 15px;
}

.leftDiv input {
  width: 95%;
  padding: 0.5rem;
  border-radius: 5px;
  margin-bottom: 0.5rem;
  height: 2rem;
  border: none;
  font-size: 1.1rem;
}

.leftDiv h1 {
  font-size: 1.5rem;
}

.saveBtn {
  padding: 0.75rem;
  cursor: pointer;
  font-size: 1.25rem;
  width: 50%;
  border-radius: 10px;
  font-weight: bold;
  color: white;
  background-color: rgb(51, 51, 51);
}

.editBtn {
  padding: 0.75rem;
  cursor: pointer;
  font-size: 1.25rem;
  width: 50%;
  border-radius: 10px;
  font-weight: bold;
  color: white;
  background-color: rgb(51, 51, 51);
}


.rightDiv {
  background-color: rgb(238, 238, 238);
  padding: 1rem;
  border-radius: 10px;
  width: 50%;
  box-shadow: rgb(0 0 0 / 35%) 0px 5px 15px;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(10, 1fr);
}

.genInfoDiv {
  grid-column: span 5;
  grid-row: span 2;
  background-color: rgb(137, 135, 253);
}

.genInfoDiv h1 {
  font-size: 2.5rem;
}

.rightDiv h3 {
  width: 200px;
  border-bottom: 1px solid grey;
}

.ExperienceSect {
  grid-row: 3/4;
  grid-column: 1/6;
}

.EducationSect {
  grid-row: 4/5;
  grid-column: 1/6;
}
